import React from 'react';
import { NewsSideBarSearch } from 'components';

const newsSideBarForm = ({ getLabel, LangConstant }) => {
  return (
    <>
      <NewsSideBarSearch getLabel={getLabel} LangConstant={LangConstant} />
      <div className="news-sidebar-form">
        <div className="news-sidebar-box-title">
          <div>
            <h2 className="news-sidebar-form-title">
              {getLabel(LangConstant.TXT_TITLE_FORM_1_NEWS)}
            </h2>
            <h2 className="news-sidebar-form-title">
              {getLabel(LangConstant.TXT_TITLE_FORM_2_NEWS)}
            </h2>
          </div>
          <h2 className="news-sidebar-form-title">
            {getLabel(LangConstant.TXT_TITLE_FORM_3_NEWS)}
          </h2>
        </div>

        <form className="news-sidebar-form-getInfo" action="">
          <input
            className="news-sidebar-form-getInfo-input"
            type="text"
            required
            placeholder={getLabel(LangConstant.TXT_INPUT_NAME_NEWS)}
          />
          <input
            className="news-sidebar-form-getInfo-input"
            type="email"
            required
            placeholder={getLabel(LangConstant.TXT_INPUT_EMAIL_NEWS)}
          />
          <button
            className="news-sidebar-form-getInfo-button-submit"
            type="submit"
          >
            {getLabel(LangConstant.TXT_INPUT_BUTTON_FORM_NEWS)}
          </button>
        </form>
      </div>
    </>
  );
};

export default newsSideBarForm;
