import React, { useEffect, useState } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Paper,
} from '@material-ui/core';
import { Delete } from '@material-ui/icons';
import { AppPagination } from 'components';
import { useStyles } from './styles';
import { AdminLayout } from 'layouts';
import { useGlobalContext } from 'store';
import { Spinner } from 'components';

const Note = () => {
  const classes = useStyles();
  const { getFeedbacks } = useGlobalContext();
  const [noteArray, setNoteArray] = useState([]);
  const [totalPage, setTotalPage] = useState();
  const [pageCurrent, setPageCurrent] = useState(1);
  const [feedbackLoading, setFeedbackLoading] = useState(true);
  useEffect(() => {
    async function run() {
      const res = await getFeedbacks();
      if (res.success) {
        if (res.total % 6 !== 0) {
          setTotalPage(Math.floor(res.total / 6) + 1);
        } else {
          setTotalPage(res.total / 6);
        }
      }
    }
    run();
  }, []);
  useEffect(() => {
    const getData = async () => {
      setFeedbackLoading(true);
      const res = await getFeedbacks(pageCurrent);
      if (res.success) {
        setNoteArray(res.data);
        setFeedbackLoading(false);
      } else {
        setNoteArray([]);
        setFeedbackLoading(false);
      }
    };
    getData();
  }, [pageCurrent]);
  return (
    <AdminLayout>
      <TableContainer className={classes.tableNote} component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell width="20%">H tn</TableCell>
              <TableCell width="20%" align="right">
                S in thoi
              </TableCell>
              <TableCell width="20%" align="right">
                Email
              </TableCell>
              <TableCell width="30%" align="right">
                Ghi ch
              </TableCell>
              <TableCell width="10%" align="right">
                Thao tc
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {noteArray?.map((item, index) => (
              <TableRow
                key={index}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <TableCell width="20%" component="th" scope="row">
                  {item.name}
                </TableCell>
                <TableCell width="20%" align="right">
                  {item.phone}
                </TableCell>
                <TableCell width="20%" align="right">
                  {item.email}
                </TableCell>
                <TableCell
                  width="30%"
                  align="right"
                  dangerouslySetInnerHTML={{ __html: item.content }}
                ></TableCell>
                <TableCell width="10%" align="right">
                  <IconButton aria-label="delete" onClick={() => {}}>
                    <Delete />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
        {feedbackLoading && <Spinner />}
      </TableContainer>
      <AppPagination
        totalPage={totalPage}
        onChange={(page) => {
          setPageCurrent(page);
        }}
      />
    </AdminLayout>
  );
};

export default Note;

export const NOTES = [
  {
    id: '1',
    name: 'a vuong',
    phone: '12345678',
    email: 'a@gmail.com',
    content: 'xinchao',
  },
];
